<template>
    <div class="container">
        <div class="row">
            <div class="col-12 col-md-6 col-lg-4 g-3">
                <div id="app">
                    <h2>會員註冊</h2>
                    <keep-alive>
                        <component :is="currentTab" @set-form="updateForm" @update-info="updateInfo" :form-data="form">
                        </component>
                    </keep-alive>
                </div>
            </div>
        </div>
    </div>
</template>

<style scoped>
    #app{
        color:aliceblue
    }
</style>

<script>
import SignUp from '@/components/SignUp.vue'
import Check from '@/components/Check.vue'
import Final from '@/components/Final.vue'

export default {
    name:'signUp',
    components: {
    SignUp,
    Check,
    Final
  },
    data(){
        return{
            tabs:['SignUp','Check','Final'],
            currentTab: 'SignUp',
            form:{}    
        }
    },
    methods:{
        updateForm(value){
            this.currentTab=value
        },
        updateInfo(value){
            this.form[value.columnName]=value.columnValue
        }
    }
}
</script>