<template>
<navbarView></navbarView>
        <div class="container">
            <div class="item_header">
                <div class="item_detail">商品</div>
                <div class="price">單價</div>
                <div class="count">數量</div> 
                <div class="amount">總計</div>
                <div class="operate">操作</div>
            </div>
            <div class="item_container" v-for="(item, index) in itemList" :key="item.id" >
                    <div class="item_header item_body">
                            <div class="item_detail">
                                <img v-bind:src="item.imgUrl" alt="">
                                <div class="name">{{item.itemName}}</div>
                            </div>
                            
                            <div class="price"><span>$</span>{{item.price}}</div>
                            <div class="count">
                                <button @click="handleSub(item)">-</button>
                                {{item.count}}
                                <button @click="handlePlus(item)">+</button>
                            </div> 
                            <div class="amount">{{item.price * item.count}}</div>
                            <div class="operate">
                                <button @click="handledelete(index)">刪除</button>
                            </div>
                    </div>
            </div>
            <br>
            <div class="item_header item_footer">
                <span>合計：</span>
                <strong id="total-amount">￥{{getTotal}}</strong>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <button @click="settleAccount()">立即結算</button>
            </div>
        </div>
</template>

<script>
import navbarView from '@/components/navbarView.vue'

export default {
    name:'Navbar',
    components: {
    navbarView
  },
  data(){
    return{
      itemList:[
        {
          id:'1',
          itemName:'Tokyo Rendez-Vous',
          imgUrl:'data:image/jpeg;base64,',
          price:'800',
          count:'0',
          select:true
        },
        {
          id:'2',
          itemName:'Sympa',
          imgUrl:'data:image/jpeg;base64,',
          price:'850',
          count:'0',
          select:true
        },
        {
          id:'3',
          itemName:'Ceremony',
          imgUrl:'data:image/jpeg;base64,',
          price:'1000',
          count:'0',
          select:true
        },
  ]

    }
  },
  computed:{
    getTotal(){
      var newArr=this.itemList.filter(function(val){
        return val.select===true;
      })
      var price=0;
      for(var i=0;i<newArr.length;i++){
        price+=newArr[i].count*newArr[i].price
      }
      return price.toFixed()
    }
  },
  methods:{
      handlePlus(item){
          item.count++;
      },
      handleSub(item){
          if(item.count>0){
          item.count--;                
          }
      },
      handledelete(index){
          console.log(this);
          this.itemList.splice(index,1);
      },
      settleAccount(){
        if(this.getTotal==0){
          alert('尚未選擇任何商品')
        }
      }
  },
}

</script>

