<template>
    <form>
        <label>姓名</label> 
        <input type="text" v-model="name" required>
        <br>
        <label>電話</label>
        <input type="text" v-model="phone" required>
        <br>
        <label>地址</label>
        <input type="text" v-model="address" required>
        <br>
        <label>信箱</label>
        <input type="email" v-model="email" required="required" pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$"/>

        <br>
        <label>密碼</label>
        <input type="password" v-model="password" required>
        <br>
        <button @click="nextStep">下一步</button>
        &nbsp;
        &nbsp;
        <router-link to="/history">返回主頁</router-link>

    </form>

</template>

<script>

export default{
    data(){
        return{
            name:'',
            phone:'',
            address:'',
            email:'',
            password:''
        }
    },
    methods:{
        nextStep(){
            if(this.name!='' && this.phone!='' && this.address!='' && this.email!='' && this.password!=''){
                this.$emit('set-form','Check')
            }
        }
    },
    watch:{
        name:{
            handler(value){
                this.$emit('updateInfo',{
                    columnName:'name',
                    columnValue:value
                })
            }
        },
        phone:{
            handler(value){
                this.$emit('updateInfo',{
                    columnName:'phone',
                    columnValue:value
                })
            }
        },
        address:{
            handler(value){
                this.$emit('updateInfo',{
                    columnName:'address',
                    columnValue:value
                })
            }
        },
        email:{
            handler(value){
                this.$emit('updateInfo',{
                    columnName:'email',
                    columnValue:value
                })
            }
        },
        password:{
            handler(value){
                this.$emit('updateInfo',{
                    columnName:'password',
                    columnValue:value
                })
            }
        }
    }
}
</script>

